    border-radius: 0;
  }

  .navbar-logo {
    position: fixed;
    top: 15px;
    left: 20px;
    scale: (0.9);
    margin: 0;
    /* transform: translate(25%, 50%); */
  }

  .menu-icon {
    display: block;
    position: absolute;
    top: -10px;
    right: 0;
    -webkit-transform: translate(-100%, 60%);
            transform: translate(-100%, 60%);
